import { motion } from 'framer-motion';
import { useState } from 'react';
import dropDownWhite from './DropdownWhite.png'

function Project3(){



    const [isOpen, setIsOpen] = useState(false);

    const container = {
        closed: {
          opacity: 0,
          height: 0,
          transition: {
            duration: 0.5,
    
          }
        },
        open: {
          opacity: 1,
          height: '220px',
          transition: {
            duration: 1.5,
    
          }
        }
      }
    
      const list = {
        closed: {
          opacity: 0,
        },
        open: {
          opacity: 1,
          transition: {
            duration: 1.5,
            delayChildren: 0.7,
            staggerChildren: 0.5
    
          }
        }
    
      }
    
      const item = {
        closed: { opacity: 0 },
        open: {
          opacity: 1,
    
        }
      }

      
      return(


    <div className="container projectContainer">
          <div id='nhl' style={{backgroundColor: 'rgb(255, 115, 0)'}} className="centerText centerDiv headerDiv projectHeader">
            <motion.img className="dropDown" src={dropDownWhite} onClick={() => { !isOpen ? setIsOpen(true) : setIsOpen(false) }}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              animate={isOpen ? { rotate: 180, transition: { duration: 0.5 } } : { rotate: 0 }}
            ></motion.img>
            <a href="https://micamedical.com/" target="_blank" class="projectName"><motion.p  whileHover={{ scale: 1.1 }} class="projectName" id="MICA">MICA Information Systems</motion.p></a>
          </div>
          <motion.div className="projectDiv centerDiv" id='project1'
            initial={{ height: 0, opacity: 0 }}
            animate={!isOpen ? 'closed' : 'open'}
            variants={container}
          >
            <motion.ul className='projectList'
              animate={!isOpen ? 'closed' : 'open'}
              variants={list}
            >
              <motion.li variants={item} className="fontify listEle">Developed using WordPress and HTML</motion.li>
              <motion.li variants={item} className="fontify listEle">Played a large role in implementation and creative processs</motion.li>
              <motion.li variants={item} className="fontify listEle">Sparked my interest in learning more about web development</motion.li>
              <motion.li variants={item} className="fontify listEle">Learned about important practical web development concepts</motion.li>
              <motion.li variants={item} className="fontify listEle">Development with a business-minded approach</motion.li>

            </motion.ul>

          </motion.div>
        </div>

      );

}

export default Project3